@import '../node_modules/antd/dist/antd.css';


.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/*
顶部样式
*/
.App .logo {
  background-image: url(./imgs/app-logo.png);
  background-size: 100%;
  width: 108px;
  height: 38px;
  background-repeat: no-repeat;
  /* width: 120px; */
  /* height: 31px; */
  /* background: rgba(255, 255, 255, 0.2); */
  margin: 16px 24px 16px 0;
  float: left;
}

.ant-layout-header{
  display: flex;
  height: 72px;
  background-color: #ffffff;
}

.ant-menu-item > a{
  color: #4a4a4a;
}

.ant-menu-horizontal .ant-menu-item-selected a{
  font-weight: bold;
  color: #4a4a4a;
}

.ant-menu-horizontal > .ant-menu-item:hover{
  color: #4a4a4a;
  border-bottom: 2px solid #4a4a4a;
}
.ant-menu-horizontal > .ant-menu-item a:hover{
  font-weight: bold;
  color: #4a4a4a;
}

.ant-menu-horizontal  .ant-menu-item-selected{
  border-bottom: 2px solid #4a4a4a;
}



.ant-layout-footer a{
  color: #999;
}

/*
首页样式
*/

.MainView{
  /* width: 1040px; */
  max-width: 980px;
  margin: 0 auto;
}

/*
pageBanner 走马灯样式
*/


.pageBanner{
  width: 940px;
}


.pageBanner .ant-carousel .slick-dots button {
  height: 5px;
}

.pageBanner .ant-layout{
  width: 100%;
  position: relative;
}

.pageBanner .ant-layout-sider{
  width: 640px;
  overflow: hidden;
}

.pageBanner .ant-layout-content{
  position: absolute;
  width: 296px;
  height: 168px;
  right: 0;
  overflow: hidden;
}

.pageBanner .ant-carousel .slick-slide {
  text-align: center;
  height: 340px;
  line-height: 340px;
  background: #364d79;
  overflow: hidden;
}

.pageBanner .ant-carousel .slick-slide h3 {
  color: #fff;
}



.BannerRecommend1{
  top: 0;
}

.BannerRecommend2{
  bottom: 0;
}

.pageBanner .bannerImg{
  height: 100%;
  width: 100%;
  border-radius: 2px;
}


/*
pageNewReleases 走马灯样式
*/

.pageNewReleases .ant-carousel .slick-slide {
  text-align: center;
  height: 300px;
  line-height: 300px;
  overflow: hidden;
  /* background: #364d79; */
}

.pageNewReleases .ant-carousel .slick-slide h3 {
  color: #fff;
}

.pageNewReleases .slick-dots li button{
  height: 10px;
  width: 10px;
  border-radius: 10px;
  background-color: black;
  
}
.pageNewReleases .ant-carousel .slick-dots li.slick-active button{
  background: #4a4a4a59;
}


.ant-layout-sider .ant-layout-sider-dark{
  flex: 0 0 640px;
  max-width: 640px;
  min-width: 640px;
  width: 640px;
}


/*
首页卡片样式
*/
.ant-card-cover{
  display: block;
  position: relative;
  width: 120px;
  height: 120px;
  overflow: hidden;
  margin: 0 auto;
}
.ant-card-meta-title{
  white-space:normal;
  margin: 0 auto;
  margin-top: 8px;
  width: 180px;
  height: 60px;
  font-size: 14px;
  font-weight: 500;
  color: #4a4a4a;
  
}
.ant-card-meta-title:hover{
  color: red;
}
.ant-card-meta-description{
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 40px;
  color: #ff434d;
}
.ant-card-body{
  padding: 0 0;
}



/*
分类标签样式
*/


.MainView .mall-all-breadcrumb{
  display: flex;
  flex-wrap: wrap;
  padding: 0px 30px 17px;
}

.MainView .item{
  float: left;
  margin-right: 40px;
  cursor: pointer;
  color: #9b9b9b;
}

.MainView .el-row{
  display: flex;
  flex-wrap: wrap;
  padding: 20px 30px;
  text-align: left;
}

.MainView .el-row a{
  color: #9b9b9b;
}

.MainView .el-col{
  width: 8.33333%;
  min-width: 32px;
}

@media (max-width: 768px){
  .MainView .item{
    margin-right: 20px;
  }

  .MainView .el-row{
    padding: 10px 18px;
  }
}


.MainView .item a:hover{
  color: #ff434d;
}

.MainView .itemIsActive a{
  font-weight: 500;
  color: #4a4a4a;
}



/*
分类应用样式
*/

.content > a {
  list-style: none;
  display: inline-block;
  text-align: center;
  width: 22%;
  font-size: 14px;
  margin: 12px 1%;

  cursor: pointer;
}
@media (max-width: 1080px){
  .content > a{
    width: 33.3%;
    padding: 20px;
    margin: 0;
  }
}

@media (max-width: 880px){
  .content > a{
    width: 50%;
    padding: 20px;
    margin: 0;
  }
}


/*
详情页面样式
*/

/*标题 简介*/
.descriptions .descriptions-title{
  color: #292525;
  font-size: 24px;
  text-align: left;
  padding: 30px 0 23px;
}
.descriptions .subtitle{
  text-align: left;
  color: #9b9b9b;
  font-size: 16px;
}


/*图片 购买*/
.descriptions .product-main{
  margin-top: 23px;
  overflow: hidden;
}

.descriptions .product-main .image-area{
  float: left;
}

.descriptions .product-main .image-area .b-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 300px;
  border: 1px solid #f4f4f4;
  background-color: gray;
}


.descriptions .product-main .image-area .b-img img{
  width: 100%;
}
.descriptions  .product-main .image-area .s-img-wrapper {
  display: flex;
  width: 300px;
  margin-top: 10px;
}


/*购买*/
.descriptions .product-main .buy-options{
  float: left;
  margin-left: 44px;
  width: 350px;
}

.descriptions .product-main .image-area .s-img-wrapper>.s-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  background-color: gray;
  margin-right: 10px;
  border: 1px solid #fafbfc;
}

.descriptions .product-main .buy-options .aline {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.descriptions .product-main .buy-options .aline .lable{
  color: #9b9b9b;
  margin-right: 16px;
  font-size: 16px;
}

.descriptions  .product-main .buy-options .aline .price {
  font-size: 24px;
  color: #ff434d;
  margin-right: 8px;
}

.descriptions .product-main .buy-options .aline .o-price {
  color: #9b9b9b;
  text-decoration: line-through;
  margin-right: 8px;
}

.descriptions .buy-options .aline .select-group {
  margin: 0 auto;
  max-width: 300px;
}

.descriptions .product-main .buy-options .aline .select-group .item {
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid #cfc8c8;
  color: #9b9b9b;
  cursor: pointer;
  margin-right: 10px;
  user-select: none;
}

.descriptions .product-main .buy-options .aline .select-group .selected {
  color: #ff434d;
  border-color: #ff434d;
}
.descriptions .product-main .buy-options .ant-btn-primary{
  background-color:rgba(255,47,58,.9); 
  border-color: rgba(255,47,58,.9); 
}

.ant-popover-inner-content{
  width: 153px;
  height: 153px;
}

/*
应用介绍
*/
.product-content{
  text-align: left;
  font-size: 16px;
}
.product-content h2{
  font-size: 24px;
  color: #4a4a4a;
  margin: 30px 0 10px;
  line-height: 1.4;
}

.product-content p{
  margin: 0 0 20px;
  line-height: 26px;
}
.product-content a{
  text-decoration: none;
  border-bottom: 1px solid #de4545;
  color: #292525;
}
.product-content a:hover{
  color: red;
}



.product-content ol{
  -webkit-padding-start: 20px;
  line-height: 32px;
  color: #292525;
}




/*
底部样式
*/

.ant-layout-footer .share{
  margin: 0 auto 60px;
  text-align: center;
  font-size: 36px;
}
.ant-layout-footer .share a{
  display: inline-block;
  height: 36px;
  width: 36px;
  margin: 0 15px;
}

.ant-layout-footer .link{
  font-size: .875rem;
  margin: 20px auto;
  text-align: center;
  padding: 0;
}

.ant-layout-footer .link li {
  color: #292525;
  list-style: none;
  display: inline-block;
  margin: 0 40px;
}

.ant-layout-footer .link li:hover {
  color: red;
  cursor: pointer;
}

.ss-img-wrapper{
  width: 100%;
}